import "./styles.css";
import bus from "./pubsub";
import Project from "./projects";
import initDOM from "./dom";
import load from './storage';

initDOM();

const plain = load();

if(plain.length){
    plain.forEach(p => {
        const proj = new Project(p.name,p.desc);
        p.toDos.forEach(t => {
            proj.addToDo(
                t.title,
                t.desc,
                t.date ? new Date(t.date) : undefined,
                t.priority
            );
            if (t.completed) proj.toDos.at(-1).toggle();
        });
    });
}
else{
    const proj = new Project("Default Project", "This is a default project");
    proj.addToDo("Here's a To-Do", "Click any To-Do fields to edit them", new Date(), "High");
}
